import * as React from 'react';

//import { Router } from 'react-router-dom';
import { Routes } from '../routes/routes';
//import history from '../services/History';
import Header from '../global-components/header/Header';
import { Footer } from '../global-components/footer/Footer';
import fetchBannerAndProductDetails from '../services/fetchBannerAndProducts';

interface Banner {
	imageUrl: string;
	heading: string;
	subHeading: string;
	code: string;
	link: string;
};

interface Product {
	name: string;
	imageUrl: string;
	description: string;
	homeEnabled: boolean;
	code: string;
	catCode: string;
	link: string;
}

export default function Main() {
	const [homePageBanner, setHomePageBanner] = React.useState<any>();
	const [homePageProducts, setHomePageProducts] = React.useState<any>();
	const [bannersAndProductsByCategory, setBannersAndProductsByCategory] = React.useState<any>();
	React.useEffect(() => {
		fetchBannerAndProductDetails(setHomePageBanner, setHomePageProducts, setBannersAndProductsByCategory);
	}, []);
	console.log(bannersAndProductsByCategory);
	return (
		<main>
			<Header />
			<Routes banner={homePageBanner} products={homePageProducts} bannersAndProductsByCategory={bannersAndProductsByCategory} />
			<Footer products={homePageProducts} />
		</main>
	)
}
