import React from 'react';
import { MDBNavbar, MDBNavbarBrand, MDBNavbarNav, MDBNavbarToggler, MDBCard, MDBCardBody, MDBIcon, MDBBtn, MDBInput, MDBRow, MDBCol, MDBCollapse, MDBNavItem, MDBNavLink, MDBContainer, MDBMask, MDBView, MDBCarouselItem, MDBCarouselInner, MDBCarousel } from 'mdbreact';
import { BrowserRouter as Router } from 'react-router-dom';


export default function ContactUs() {

	return (
		<section >
			<MDBContainer fluid>
				<MDBRow className="mb-4">
					<img width="100%" height="750px" src="/static/contant-us.png" className="" alt="" />
				</MDBRow>
			</MDBContainer>
			<MDBContainer className="contact-us-container h-100 z-depth-1-half p-4 my-4">
				<MDBRow>
					<MDBCol lg="7">
						<div
							id="map-container"
							className="map-container h-100">
							<iframe
								src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3888.5352052127555!2d77.53673891463535!3d12.93756751912513!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x0!2zMTLCsDU2JzE1LjIiTiA3N8KwMzInMjAuMSJF!5e0!3m2!1sen!2sin!4v1593714063162!5m2!1sen!2sin"
								title="This is a unique title"
								width="100%"
								height="100%"
								frameBorder="0"
								style={{ border: 0 }} />
						</div>
					</MDBCol>
					<MDBCol>
						<h2 className="font-weight-bold text-center drkColorCd">Contact Us</h2>
						<MDBRow md="4" className="my-3">
							<MDBIcon icon="map-marker-alt" className="p-3" />
							<MDBCol className="contact-us-details">
								<div>ANVAYAM PVT LTD</div>
								<div>P.O. Box 283 8562 Fusce Rd.</div>
								<div>Frederick Nebraska 20620</div>
							</MDBCol>
						</MDBRow>
						<MDBRow md="4" className="my-3">
							<MDBIcon icon="phone" className="p-3" />
							<MDBCol className="contact-us-details">
								<div>+ 01 234 567 89</div>
								<div>Mon - Fri, 8:00-22:00</div>
							</MDBCol>
						</MDBRow>
						<MDBRow md="4" className="my-3">
							<MDBIcon icon="envelope" className="p-3" />
							<MDBCol className="contact-us-details">
								<div>info@gmail.com</div>
								<div>sale@gmail.com</div>
							</MDBCol>
						</MDBRow>
						<MDBCard>
							<MDBCardBody>
								<form>
									<p className="h5 text-center mb-4">Write to us</p>
									<div className="grey-text">
										<MDBInput label="Your name" icon="user" group type="text" validate error="wrong"
											success="right" />
										<MDBInput label="Your email" icon="envelope" group type="email" validate error="wrong"
											success="right" />
										<MDBInput label="Subject" icon="tag" group type="text" validate error="wrong" success="right" />

										<MDBInput type="textarea" rows="2" label="Your message" icon="pencil-alt" />
									</div>
									<div className="text-center">
										<MDBBtn outline className="send-btn" >
											Send
            					<MDBIcon far icon="paper-plane" className="ml-1" />
										</MDBBtn>
									</div>
								</form>
							</MDBCardBody>
						</MDBCard>
					</MDBCol>
				</MDBRow>
			</MDBContainer>
		</section>
	);
}