import React from 'react';
import { MDBNavbar, MDBNavbarBrand, MDBNavbarNav, MDBNavbarToggler, MDBCollapse, MDBNavItem, MDBNavLink, MDBContainer, MDBMask, MDBView, MDBCarouselItem, MDBCarouselInner, MDBCarousel, MDBMedia, MDBRow, MDBCol } from 'mdbreact';
import { BrowserRouter as Router, useLocation } from 'react-router-dom';
import { Parallax, withController, ParallaxBanner } from 'react-scroll-parallax';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router-dom';

type State = {
	collapse: boolean;
	isWideEnough: boolean;
}
class Header extends React.Component<any, State>{
	constructor(props: any) {
		super(props);
		this.state = {
			collapse: false,
			isWideEnough: false,
		};
		this.onClick = this.onClick.bind(this);
		this.isTransparent = this.isTransparent.bind(this);
	}
	isTransparent = () => {
		const location = this.props.location.pathname;
		console.log(location)
		return location == "/contact-us" ? false : true;
	}
	onClick() {
		this.setState({
			collapse: !this.state.collapse,
		});
	}
	componentDidUpdate() {
		//	this.props.parallaxController.update();
	}

	render() {
		return (
			<div>
				<header>

					<MDBNavbar color="nav-bar-bg-color" fixed="top" dark expand="md" scrolling transparent={this.isTransparent()}>
						<MDBContainer>
							<MDBNavbarBrand href="/" className="custom-nav-bar-brand">
								<img src="/static/logo-svg.svg" height="80px" width="80px" className="img-fluid" alt="" />
							</MDBNavbarBrand>
							{!this.state.isWideEnough && <MDBNavbarToggler onClick={this.onClick} />}
							<MDBCollapse isOpen={this.state.collapse} navbar>
								<MDBNavbarNav right class="txtColorCd">
									<MDBNavItem>
										<MDBNavLink as={Link} to="/">Home</MDBNavLink>
									</MDBNavItem>
									<MDBNavItem>
										<MDBNavLink as={Link} to="/about-us">About us</MDBNavLink>
									</MDBNavItem>
									<MDBNavItem>
										<MDBNavLink as={Link} to="/contact-us">Contact us</MDBNavLink>
									</MDBNavItem>
									<MDBNavItem>
										<MDBNavLink as={Link} to="/products">Products</MDBNavLink>
									</MDBNavItem>
								</MDBNavbarNav>
							</MDBCollapse>
						</MDBContainer>
					</MDBNavbar>
				</header>
			</div >
		);
	}
}

export default withRouter(Header);