import * as React from 'react';
import { Carousel, Container, Card, Row, Col } from 'react-bootstrap';
import { useParams } from 'react-router-dom';
import { MDBNavbar, MDBFooter, MDBNavbarBrand, MDBNavbarNav, MDBNavbarToggler, MDBCard, MDBCardBody, MDBIcon, MDBBtn, MDBInput, MDBRow, MDBCol, MDBCollapse, MDBNavItem, MDBNavLink, MDBContainer, MDBMask, MDBView, MDBCarouselItem, MDBCarouselInner, MDBCarousel, MDBCardTitle, MDBCardText } from 'mdbreact';
import { useController, ParallaxBanner, Parallax } from 'react-scroll-parallax';

import { useMediaQuery } from 'react-responsive';

export const Products: React.FC<any> = ({ bannersAndProductsByCategory }) => {
	let { productId } = useParams();
	//let { parallaxController } = useController();
	const { parallaxController } = useController();
	const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1224px)' })
	const isTabletOrMobileDevice = useMediaQuery({
		query: '(max-device-width: 1224px)'
	})
	const parallaxDisabled = isTabletOrMobile || isTabletOrMobileDevice;
	React.useLayoutEffect(() => {
		const handler = () => parallaxController.update();
		window.addEventListener('load', handler);
		return () => window.removeEventListener('load', handler);
	}, [parallaxController]);
	return (
		<section style={{ backgroundColor: "#f0f0f4" }}>
			<MDBView >
				<img style={{ height: '100vh' }} width="100%" src="/static/embedded-anm.jpg" alt="Carousel"></img>
			</MDBView>
			<MDBContainer fluid className="intro-container" >
				<MDBContainer className="intro-box py-5"  >
					<MDBCardTitle className="txtColorCd font-weight-bold">Our range of Products</MDBCardTitle>
					<MDBRow>
						<MDBCol className="text-white">
							Founded in 2020, ANVAYAM emerged as a key contender in the Embedded Software Services arena and went on to establish itself as a premier player in the IoT domain in a very short span of time.
						</MDBCol>
					</MDBRow>

				</MDBContainer>
			</MDBContainer>
			{bannersAndProductsByCategory && bannersAndProductsByCategory.map((item: any) => (
				<section>
					<MDBContainer fluid className="px-0" id={item.banner.code}>
						<MDBView>
							<ParallaxBanner
								className="d-flex"
								layers={[
									{
										image: item.banner.imageUrl,
										amount: 0.1,
									},
									{
										amount: -0.2,
										children: <div className={`banner-overlay`}>
											<MDBContainer className="banner-container">
												<div className="d-flex flex-column justify-content-center align-items-center">
													<h3 className="text-center">
														{item.banner.heading}
													</h3>
												</div>
											</MDBContainer>
										</div>
									}
								]}
								style={{
									height: '20vh',
								}}
							/>
						</MDBView>
					</MDBContainer>
					{item.products && item.products.map((product: any, index: number) => (
						<MDBContainer id={product.code} className="productData pb-4" style={{ backgroundColor: `${index % 2 ? "#e5eff4" : "#d9dce6"}` }}>
							<MDBRow>
								<MDBCol>
									<h2 className="w-100 my-4 font-weight-bold drkColorCd">
										{product.name}
									</h2>
								</MDBCol>
							</MDBRow>
							<MDBRow>
								<MDBCol md="6" className={parallaxDisabled ? `order-1` : `order-${index % 2}`}>
									<div className="pdct">
										<ul className="p-0">
											<li>We combine multiple discipline capabilities and deliver technologically advanced sensor and actuator ecosystem from concept to prototyping, testing and full production.</li>
											<li>We develop innovative, low power and wireless sensors to integrate into IoT system and solutions, while our actuators are the perfect solution for simple, safe and clean movement with accurate and smooth motion control.</li>
											<li>Our sensors and sensor systems, which are produced via qualified industrial processes result in increased accuracy, decreased size and cost, and the ability to measure or detect things that weren't previously possible.</li>
											<li>Our flexibility and dedication to the client ensure a superior custom-made and error-free product within a limited time frame.</li>
										</ul>
									</div>
								</MDBCol>
								<MDBCol md="6" className={parallaxDisabled ? `order-0` : `order-${(index + 1) % 2}`}>
									<Parallax className="product-parallax-img-wrapper" disabled={parallaxDisabled} y={[0, 30]} tagOuter="figure">
										<MDBCard><img src={product.imageUrl} /></MDBCard>
									</Parallax>
								</MDBCol>
							</MDBRow>
						</MDBContainer>
					))}
				</section>
			))}
		</section >
	)
}