import Axios from "axios"
import axios from 'axios';
import Content from '../assets/content.json';
import { Void } from "react-horizontal-scrolling-menu/build/types";


const mapResponseToProductDetails = (response: any) => {
	return response.map((rsp: any) => ({
		name: rsp.name,
		imageUrl: rsp.imgUrl,
		description: rsp.shortDesc,
		homeEnabled: rsp.isHomeEnable,
		code: rsp.prodCode,
		catCode: rsp.catCode,
		link: "/products#" + rsp.prodCode
	}));
};
const mapResponseToBannerDetails = (response: any) => {
	return response.map((rsp: any) => ({
		imageUrl: rsp.imgUrl,
		heading: rsp.name,
		subHeading: rsp.shortDesc,
		code: rsp.code,
		link: "products#" + rsp.code
	}));
};
const groupBy = (arr: any, property: any) => {
	return arr.reduce((acc: any, obj: any) => {
		const key = obj[property];
		if (!acc[key]) {
			acc[key] = [];
		}
		// Add object to list for given key's value
		acc[key].push(obj);
		return acc;
	}, {});
};

const groupProductsByCategory = (products: any, banners: any) => {
	const bannersByCategory = groupBy(banners, "code");
	const productsByCategory = groupBy(products, "catCode");
	console.log(bannersByCategory, productsByCategory);
	const result: any = [];
	const catCodes = Object.keys(bannersByCategory);
	catCodes.forEach((catCode) => {
		result.push({
			banner: bannersByCategory[catCode][0],
			products: productsByCategory[catCode]
		})
	});
	console.log(result);
	return result;
}
const fetchBannerAndProductDetails = (
	setHomePageBanner: (response: any) => (Void),
	setHomePageProducts: (response: any) => (Void),
	setBannersAndProductsByCategory: (response: any) => (Void)
) => {
	axios.get(`/content`)
	.then(res => {
		const data = res.data
		const bannerDetails = mapResponseToBannerDetails(data[0].bannerContent)
		const productDetails = mapResponseToProductDetails(data[1].productDetails)
		console.log(bannerDetails, productDetails)
		const bannersAndProductsByCategory = groupProductsByCategory(productDetails, bannerDetails)
		setHomePageBanner(bannerDetails);
		setHomePageProducts(productDetails);
		setBannersAndProductsByCategory(bannersAndProductsByCategory);
	}).catch((err) => {
		console.error(err);
	})
	// const data = Content;
	// console.log(data);
	// const bannerDetails = mapResponseToBannerDetails(data.bannerContent)
	// const productDetails = mapResponseToProductDetails(data.productDetails);
	// console.log(bannerDetails, productDetails)
	// const bannersAndProductsByCategory = groupProductsByCategory(productDetails, bannerDetails)
	// setHomePageBanner(bannerDetails);
	// setHomePageProducts(productDetails);
	// setBannersAndProductsByCategory(bannersAndProductsByCategory);
}

export default fetchBannerAndProductDetails;