import * as React from 'react';
import { Switch } from 'react-router-dom';
import { Route } from 'react-router-dom';

import { Home } from '../home/Home';
import { About } from '../about/About';
import { Products } from '../products/Products';
import ContactUs from '../contact-us/ContactUs';

import fetchBannerAndProductDetails from '../services/fetchBannerAndProducts';

interface Banner {
	imageUrl: string;
	heading: string;
	subHeading: string;
	code: string;
	link: string;
};

interface Product {
	name: string;
	imageUrl: string;
	description: string;
	homeEnabled: boolean;
	code: string;
	catCode: string;
	link: string;
}
interface RouteProps {
	banner: Banner[];
	products: Product[];
	bannersAndProductsByCategory: any;
}

export const Routes: React.FC<RouteProps> = ({ banner, products, bannersAndProductsByCategory }) => {
	console.log(bannersAndProductsByCategory)
	return (
		<Switch>
			<Route path="/home" exact><Home banner={banner} products={products} /></Route>
			<Route path="/about-us" ><About /></Route>
			<Route path="/contact-us" ><ContactUs /></Route>
			<Route path="/products" ><Products bannersAndProductsByCategory={bannersAndProductsByCategory} /></Route>
			<Route><Home banner={banner} products={products} /></Route>
		</Switch>
	);
}
